	module.exports = {"data":{"markdownRemark":{"headings":[{"value":"About Elinor Infant","depth":1},{"value":"The problem","depth":2},{"value":"The process","depth":2},{"value":"The Solution","depth":2}],"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.55%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Elinor Infant Specimen Booklet, Poster Spread\"\n        title=\"\"\n        src=\"/static/type_01-d70db7035ca985f541f0b6ab80350297-755ae.jpg\"\n        srcset=\"/static/type_01-d70db7035ca985f541f0b6ab80350297-f07fc.jpg 125w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-9d06f.jpg 250w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-755ae.jpg 500w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-63ca3.jpg 750w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-9b9a3.jpg 1000w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-6e031.jpg 1500w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h1>About Elinor Infant</h1>\n<p>Elinor Infant is a friendly humanist typeface created for children learning to read and write. Its rounded terminals and open counters eliminate potential distractions, enhance character recognition and motivate children to engage with the learning process through a fun and interesting experience.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 36.25%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Uppercase Alphabet\"\n        title=\"\"\n        src=\"/static/type_03-0739a55451539369fd405b301940cf53-755ae.jpg\"\n        srcset=\"/static/type_03-0739a55451539369fd405b301940cf53-f07fc.jpg 125w,\n/static/type_03-0739a55451539369fd405b301940cf53-9d06f.jpg 250w,\n/static/type_03-0739a55451539369fd405b301940cf53-755ae.jpg 500w,\n/static/type_03-0739a55451539369fd405b301940cf53-63ca3.jpg 750w,\n/static/type_03-0739a55451539369fd405b301940cf53-9b9a3.jpg 1000w,\n/static/type_03-0739a55451539369fd405b301940cf53-6e031.jpg 1500w,\n/static/type_03-0739a55451539369fd405b301940cf53-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>The problem</h2>\n<p>When children learn to read and write, they make great efforts to understand every letter of the alphabet and the connection between letters and sounds. They first learn to recognize lowercase characters, then uppercase, then words and then sentences. During this process, many children tend to feel overwhelmed and discouraged, however, one can support them by providing them with inviting texts properly set in typefaces with good readability and legibility.</p>\n<p>Young readers are able to fully discriminate small visual details like adults do. However, within their immature cognitive process, their memory and attention fluctuate; thus, a variation in the smallest details or quirks in shapes might trigger confusion. For this reason, the x-height, counter size, letter spacing and stroke width, as well as its presentation, in terms of size and colour, play a great role in the effectiveness of a font for a kid.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.55%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Character \"G\" upper and lowercase design in Glyphs\"\n        title=\"\"\n        src=\"/static/type_02-c053754507feb7b0dde95e5636b12c18-755ae.jpg\"\n        srcset=\"/static/type_02-c053754507feb7b0dde95e5636b12c18-f07fc.jpg 125w,\n/static/type_02-c053754507feb7b0dde95e5636b12c18-9d06f.jpg 250w,\n/static/type_02-c053754507feb7b0dde95e5636b12c18-755ae.jpg 500w,\n/static/type_02-c053754507feb7b0dde95e5636b12c18-63ca3.jpg 750w,\n/static/type_02-c053754507feb7b0dde95e5636b12c18-9b9a3.jpg 1000w,\n/static/type_02-c053754507feb7b0dde95e5636b12c18-6e031.jpg 1500w,\n/static/type_02-c053754507feb7b0dde95e5636b12c18-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>The process</h2>\n<p>The design process to create Elinor Infant involved a deep research about the learning process, an analysis of existing typefaces, and the study of every letterform on the alphabet; to ultimately understand the anatomy of each character and simplify it for children. </p>\n<p>To reduce potential distraction, medium weight consistent strokes with slightly rounded terminals were carried through all the characters. The x-height was set large to increase legibility and provide space for open counters. The contrast between the thick and thins was accommodated to look comfortable to the eye but adjusted not to distract the child. Ink traps were added in letters with diagonals to maintain the font colour in multiple sizes. The ascenders were designed to be higher than the cap height to enhance character recognition between upper and lower case. All characters were designed as single-story to avoid confusion.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 70.55%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Typeface Features\"\n        title=\"\"\n        src=\"/static/type_04-bd54d26d4d71c69c39ee9c247e32593e-755ae.jpg\"\n        srcset=\"/static/type_04-bd54d26d4d71c69c39ee9c247e32593e-f07fc.jpg 125w,\n/static/type_04-bd54d26d4d71c69c39ee9c247e32593e-9d06f.jpg 250w,\n/static/type_04-bd54d26d4d71c69c39ee9c247e32593e-755ae.jpg 500w,\n/static/type_04-bd54d26d4d71c69c39ee9c247e32593e-63ca3.jpg 750w,\n/static/type_04-bd54d26d4d71c69c39ee9c247e32593e-9b9a3.jpg 1000w,\n/static/type_04-bd54d26d4d71c69c39ee9c247e32593e-6e031.jpg 1500w,\n/static/type_04-bd54d26d4d71c69c39ee9c247e32593e-2a439.jpg 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>The Solution</h2>\n<p>This sans serif font with humanist features is an easy-to-read typeface with good readability and legibility for children. Elinor Infant manages to keep the young reader’s attention for a long time through a constant stroke weight; it’s simple shapes and smooth curves let children understand the letters’ structure to effectively learn how to read them and write them. </p>\n<p>Elinor Infant was designed as a display typeface for preschool classrooms and textbooks with short line length. It should be featured as the first typeface that kids will encounter, Elinor Infant invites children to read through a warm, friendly and well-designed typeface for them.</p>","frontmatter":{"title":"Elinor Infant","graduate":"Claudia Corrales","category":"Typography","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/static/type_01-d70db7035ca985f541f0b6ab80350297-a7a62.jpg","srcSet":"/static/type_01-d70db7035ca985f541f0b6ab80350297-5a1a9.jpg 375w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-0908c.jpg 750w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-a7a62.jpg 1500w,\n/static/type_01-d70db7035ca985f541f0b6ab80350297-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/elinor-infant/","name":"/Elinor Infant/"}}

